body{color:#9da4b4;background-color:#f3f3f3;font-family:'Baloo Paaji 2', cursive;font-family:'Roboto Mono', monospace;font-family:'Varela Round', sans-serif}.search-input{max-width:500px;width:100%;margin:0 auto;display:flex;flex-direction:row;justify-content:start}.search-input input{outline:none;font-size:16px;padding:3px;width:100%}.search-input .clear-button{background-color:silver;color:white;cursor:pointer}.cards{margin:0 auto;width:100%;max-width:500px;display:flex;flex-direction:column;justify-content:start;font-size:16px}.cards .card{background-color:white;border-bottom:1px solid silver;color:#9da4b4;padding:1rem}.cards .card .card-title{font-weight:bold;font-size:1.0em;margin-bottom:1rem}.cards .card .card-text{color:#bcc3c9}.cards .card .card-tags{margin-top:1rem}.cards .card textarea{overflow:auto;width:100%;height:100%;min-height:10rem}
